<template>
	<div class="container-box" :style="{'background-color': bgColor}">
		<div class="header-box" :style="{color: primaryColor}">
			<div :class="icon" class="iconfont header-icon"></div>
			<div>{{ title }}</div>
		</div>
		<slot name="container"></slot>
	</div>
</template>

<script>
export default {
	props: {
		// 标题旁的icon
		icon: {
			type: String,
			default: 'icon-shexiangtou'
		},
		// 标题名称
		title: {
			type: String,
			default: '视频监控'
		},
		// 主标题颜色
		primaryColor: {
			type: String,
			default: '#1DE9B6'
		},
		bgColor: {
			type: String,
			default: '#041A2D'
		}
	}
};
</script>

<style scoped lang="scss">
.container-box {
	width: 1420px;
	padding: 20px 0 20px;
	.header-box {
		width: 100%;
		display: flex;
		height: 20px;
		justify-content: center;
		align-items: center;
		font-size: 18px;
		.header-icon {
			font-size: 20px;
			margin-right: 10px;
			margin-top: 2px;
		}
	}
}
</style>



